import React, { useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { FormGroup, Label, Button , Input} from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import { Formik, Field, Form, FastField } from 'formik'
import * as Yup from 'yup'

const formSchema = Yup.object().shape({
  password: Yup.string().required(
    <FormattedMessage id='createPassword.password.required' />
  ),
  enterThePassword: Yup.string().required(
    <FormattedMessage id='createPassword.password.required' />
  )
})

const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/

const ProvideNewPassword = () => {
  const [isAppcepted, setIsAppcepted] = useState(false)
  const [isNotApccepted, setIsNotAccepted] = useState(false)

  const onSubmit = (values, actions) => {
    setTimeout(() => {
      if (!isAppcepted) {
        setIsNotAccepted(true)
        return
      }
      alert(JSON.stringify(values, null, 2))
      actions.setSubmitting(false)
    }, 1000)
  }

  return (
    <Formik
    initialValues={{
      password: '',
      enterThePassword: ''
    }}
      onSubmit={onSubmit}
      validationSchema={formSchema}
    >
      {({ errors, touched }) => (
        <Form>

        <div>
          <span className = 'd-flex justify-content-center'>
            <FormattedMessage id='provideNewPassword.title'/>
          </span>
        </div> 
    
          <FormGroup className='form-label-group position-relative mt-2'>
            <FormattedMessage id='provideNewPassword.password'>
              {(msg) => (
                <React.Fragment>
                  <FastField name='password'>
                    {({ field, form }) => (
                      <Input
                        type='password'
                        className={`form-control ${
                          errors.password && touched.password && 'is-invalid'
                        }`}
                        placeholder={msg}
                        {...field}
                        onChange={(e) =>
                          form.setFieldValue('password', e.target.value)
                        }
                      />
                    )}
                  </FastField>
                  {errors.password && touched.password ? (
                    <div className='text-danger'>{errors.password}</div>
                  ) : null}
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>

          <FormGroup className='form-label-group position-relative mt-3'>
            <FormattedMessage id='provideNewPassword.enterThePassword'>
              {(msg) => (
                <React.Fragment>
                  <Field
                    name='enterThePassword'
                    className={`form-control ${
                      errors.enterThePassword && touched.enterThePassword && 'is-invalid'
                    }`}
                    placeholder={msg}
                  />
                  {errors.enterThePassword && touched.enterThePassword ? (
                    <div className='text-danger'>{errors.enterThePassword}</div>
                  ) : null}
                  <Label>{msg}</Label>
                </React.Fragment>
              )}
            </FormattedMessage>
          </FormGroup>

          <div>
                <FormattedMessage id='createPassword.condition.1'/>
          </div>
          <div>
                <FormattedMessage id='createPassword.condition.2'/>
          </div>
          <div>
                <FormattedMessage id='createPassword.condition.3'/>
          </div>
          
          <div className='d-flex justify-content-center mt-2'>
            <Button color='primary' type='submit'>
              <FormattedMessage id='provideNewPassword.continutes' />
            </Button>
          </div>
        </Form>
      )}
    </Formik>
  )
}

export default ProvideNewPassword
