import React, { useEffect, useState } from 'react'
import { FormattedMessage } from 'react-intl'
import { FormGroup, Label, Button, Input } from 'reactstrap'
import '../../assets/scss/pages/authentication.scss'
import { Formik, Field, Form, FastField } from 'formik'
import * as Yup from 'yup'
import BaseFormGroup from '../../components/BaseFormGroup'
import { useHistory } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { createPassword, saveRegisterToken } from '../../redux/actions/auth'

const formSchema = Yup.object().shape({
  password: Yup.string().required(
    <FormattedMessage id='createPassword.password.required' />
  ),
  enterThePassword: Yup.string().oneOf(
    [Yup.ref('password'), null],
    <FormattedMessage id='createPassword.passwordMustMatch' />
  )
})

const CreatePassword = ({ isLanding2 }) => {
  const history = useHistory()
  const dispatch = useDispatch()
 
  useEffect(() => {
    const code =
      new URLSearchParams(document.location.search).get('code')
    if (!code) {
      history.push('/')
    } else {
      dispatch(saveRegisterToken(code))
      history.push(history.location.pathname)
    }
  }, [])

  const onClickContinue = (values) => {
    dispatch(createPassword(values.password))
  }

  return (
    <Formik
      initialValues={{
        password: '',
        repeatePassword: ''
      }}
      onSubmit={onClickContinue}
      validationSchema={formSchema}
    >
      {({ errors, touched }) => (
        <Form className=''>
          <div className='text-center mb-3'>
            <h4 className='font-weight-bold'>
              <FormattedMessage id='createPassword.title' />
            </h4>
          </div>
          <BaseFormGroup
            messageId='login.password'
            fieldName='password'
            errors={errors}
            touched={touched}
          />
          <BaseFormGroup
            messageId='createPassword.enterThePassword'
            fieldName='repeatePassword'
            errors={errors}
            touched={touched}
          />
          <div>
            <FormattedMessage id='createPassword.condition.1' />
          </div>
          <div>
            <FormattedMessage id='createPassword.condition.2' />
          </div>
          <div>
            <FormattedMessage id='createPassword.condition.3' />
          </div>

          <div className='d-flex justify-content-center mt-2'>
            <Button color='primary' type='submit'>
              <FormattedMessage
                id={
                  isLanding2
                    ? 'createPassword.continutes'
                    : 'createPassword.done'
                }
              />
            </Button>
          </div>
        </Form>
      )}
    </Formik>
  )
}

export default CreatePassword
