import React from 'react'
import Select from '../../components/Select'
import { FormattedMessage, injectIntl } from 'react-intl'
import { Form, Formik } from 'formik'
import '../../assets/scss/pages/complete-infor.scss'
import { FormGroup, Row, Col, Button, Label } from 'reactstrap'
import * as Yup from 'yup'
import BaseFormGroup from '../../components/BaseFormGroup'
import DatePicker from '../../components/DatePicker'
import { Link } from 'react-router-dom'

const CompleteInforValidate = Yup.object().shape({
  nbrPer: Yup.string().required(
    <FormattedMessage id='completeInformation.nbrPer.required' />
  ),
  dateOfBirth: Yup.string().required(
    <FormattedMessage id='completeInformation.dateOfBirth.required' />
  ),
  address: Yup.string().required(
    <FormattedMessage id='completeInformation.address.required' />
  ),
  branch: Yup.string().required(
    <FormattedMessage id='completeInformation.branch.required' />
  ),
  accountNbr: Yup.string().required(
    <FormattedMessage id='completeInformation.accountNbr.required' />
  )
})

const personalInfoOptions = [
  { value: 'passport', label: 'H chiu', color: '#338955', isFixed: true },
  {
    value: 'id',
    label: 'Chng minh nhn dn',
    color: '#338955',
    isFixed: true
  },
  {
    value: 'idNew',
    label: 'Cn cc cng dn',
    color: '#338955',
    isFixed: true
  }
]

const genderOptions = [
  { value: '1', label: 'Nam' },
  { value: '2', label: 'N' },
  { value: '3', label: 'Khc' }
]

const bank = [
  { value: '1', label: 'Tien Phong Bank' },
  { value: '2', label: 'Vietcombank' },
  { value: '3', label: 'BIDV' }
]

const CompleteInformation = ({ intl }) => {

  const renderSelect = (option, fieldName, msgField) => {
    return (
      <FormattedMessage id={msgField}>
        {(msg) => (
          <FormGroup>
            <Select
              placeholder={msg}
              className='form-label-group position-relative'
              classNamePrefix='Select'
              name={fieldName}
              options={option}
              onChange={() => {}}
            />
          </FormGroup>
        )}
      </FormattedMessage>
    )
  }

  const onSubmit = (values) => {
    setTimeout(() => {
      alert(JSON.stringify(values, null, 2))
      actions.setSubmitting(false)
    }, 1000)
  }

  return (
    <div className='completeInfor'>
      <Formik
        initialValues={{
          personalInfo: '',
          nbrPer: '',
          dateOfBirth: '',
          gender: '',
          province: '',
          district: '',
          wards: '',
          address: '',
          gif: '',
          bank: '',
          branch: '',
          accountNbr: ''
        }}
        validationSchema={CompleteInforValidate}
        onSubmit={onSubmit}
      >
        {({ errors, touched }) => (
          <Form>
            <Row>
              {/* form left */}
              <Col sm='12' lg='3' className='mb-3'>
                <Row className='ml-2'>
                  <Label className='font-weight-bold'>
                    <FormattedMessage id='register.fullname' />
                  </Label>
                </Row>
                <Row className='ml-3'>
                  <span className='text-gray'>L Hng Ngc</span>
                </Row>
                <Row className='ml-2  mt-2'>
                  <Label className='font-weight-bold'>
                    <FormattedMessage id='register.phoneNumber' />
                  </Label>
                </Row>
                <Row className='ml-3'>
                  <span className='text-gray'>0123456789</span>
                </Row>
                <Row className='ml-2  mt-2'>
                  <Label className='font-weight-bold'>Email*</Label>
                </Row>
                <Row className='ml-3'>
                  <span className='text-gray'>abc@gmail.com</span>
                </Row>
              </Col>

              {/* form right */}
              <Col sm='12' lg='9'>
                <Row>
                  <Col sm='6'>
                    {renderSelect(
                      personalInfoOptions,
                      'personalInfo',
                      'completeInformation.idType'
                    )}
                  </Col>
                  <Col sm='6'>
                    <BaseFormGroup
                      messageId='completeInformation.nbrPer'
                      fieldName='nbrPer'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                </Row>
                <Row>
                  <Col sm='6'>
                    <DatePicker
                      className='form-control bg-white'
                      placeholder={intl.formatMessage({
                        id: 'completeInformation.dateOfBirth'
                      })}
                      name='dateOfBirth'
                      value={new Date()}
                      options={{ dateFormat: 'M \\ d \\, Y' }}
                      onChange={(date) => {}}
                    />
                  </Col>
                  <Col sm='6'>
                    {renderSelect(bank, 'gender', 'completeInformation.gender')}
                  </Col>
                </Row>
                <Row>
                  <Col sm='4'>
                    {renderSelect(
                      bank,
                      'province',
                      'completeInformation.province'
                    )}
                  </Col>
                  <Col sm='4'>
                    {renderSelect(
                      bank,
                      'district',
                      'completeInformation.district'
                    )}
                  </Col>
                  <Col sm='4'>
                    {renderSelect(bank, 'wards', 'completeInformation.wards')}
                  </Col>
                </Row>

                <Row>
                  <Col sm='8'>
                    <BaseFormGroup
                      messageId='completeInformation.address'
                      fieldName='address'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='4'>
                    <BaseFormGroup
                      messageId='completeInformation.gif'
                      fieldName='gif'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                </Row>

                <Row>
                  <Col sm='4'>
                    {renderSelect(bank, 'bank', 'completeInformation.bank')}
                  </Col>
                  <Col sm='4'>
                    <BaseFormGroup
                      messageId='completeInformation.branch'
                      fieldName='branch'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                  <Col sm='4'>
                    <BaseFormGroup
                      messageId='completeInformation.accountNbr'
                      fieldName='accountNbr'
                      errors={errors}
                      touched={touched}
                    />
                  </Col>
                </Row>
                <div className='d-flex justify-content-center justify-content-md-end'>
                  <Link to='/create-password'>
                    <Button.Ripple type='button'>
                      <FormattedMessage id='completeInformation.back' />
                    </Button.Ripple>
                  </Link>
                  <Button.Ripple type='submit' className='ml-2'>
                    <FormattedMessage id='completeInformation.done' />
                  </Button.Ripple>
                </div>
              </Col>
            </Row>
          </Form>
        )}
      </Formik>
    </div>
  )
}
export default injectIntl(CompleteInformation)
