import { FormattedMessage } from 'react-intl'
import { toast } from 'react-toastify'
import { API_R_200, REMEMBER_ME_TOKEN } from '../../../configs/app-configs'
import { history } from '../../../history'
import { AuthService } from '../../../services/authService'
import { errorMessage } from '../../../utility/HttpClient'
import React from 'react'
export const LOGIN_ACTION = 'LOGIN_ACTION'
export const LOGIN_FAIL_ACTION = 'LOGIN_FAIL_ACTION'
export const LOGOUT_ACTION = 'LOGOUT_ACTION'
export const SAVE_REGISTER_TOKEN = 'SAVE_REGISTER_TOKEN'

export const checkLoginStatus = (authToken) => {
  return async (dispatch, getState) => {
    try {
      let respone = await AuthService.checkLoginByToken()
      if (respone.status === API_R_200) {
        respone = await AuthService.getUserInfo(
          getState().auth.user.username,
          authToken
        )
        dispatch({
          type: LOGIN_ACTION,
          payload: { authToken, user: respone.data || {} }
        })
        history.push(history.location.pathname)
      } else {
       dispatch({ type: LOGOUT_ACTION })
      }
    } catch (error) {
      console.log(error)
      // dispatch({ type: LOGOUT_ACTION })
    }
  }
}

export const loginAction = (user) => {
  return async (dispatch) => {
    try {
      let respone = await AuthService.login(user)
      if (respone.status === API_R_200) {
        const authToken = respone.data.id_token
        respone = await AuthService.getUserInfo(user.username, authToken)
        if (user.isRemeberMe) {
          localStorage.setItem(
            REMEMBER_ME_TOKEN,
            JSON.stringify({
              username: user.username,
              name: respone.data.fullName
            })
          )
        }
        dispatch({
          type: LOGIN_ACTION,
          payload: { authToken, user: respone.data || [] }
        })
        history.push('/')
      } else {
        const token = { authToken: 'authToken' , user: {}}
        dispatch({ type: LOGIN_ACTION, payload: token })
        toast.error(errorMessage(<FormattedMessage id='login.fail' />))
      }
    } catch (error) {
      console.log(error)
    }
  }
}

export const createPassword = (password) => {
  return async (dispatch, getState) => {
    try {
      const respone = await AuthService.createPassword(password, getState().auth.registerToken)
      if (respone.status === 200 && respone.data) {
        history.push('/complete-information')
      }
    } catch (error) {}
  }
}

export const saveRegisterToken = (registerToken) => {
  return (dispatch) =>
    dispatch({ type: SAVE_REGISTER_TOKEN, payload: registerToken })
}

export const logoutAction = () => {
  return async (dispatch) => {
    try {
      dispatch({ type: LOGOUT_ACTION })
    } catch (error) {
      history.push('/')
      dispatch({ type: LOGOUT_ACTION })
    }
  }
}
